export default {
	data() {
		return {
			shopItem: {}
		};
	},
	// uni-app既支持原生小程序的生命周期函数 同时也支持Vue组件的生命周期函数
	//  原生小程序的生命周期函数
	onLoad(options){
		this.shopItem = JSON.parse(options.shopItem)
	},
	// Vue组件的生命周期函数
	mounted() {
		console.log('mounted()');
	}
}
